@import '~tailwindcss/base';
@import '~tailwindcss/components';
@import '~tailwindcss/utilities';

.navbar {
  @apply bg-blue-800 py-4;
}

.navbar-brand {
  @apply text-white-700 font-bold text-lg mr-4;
}

.nav-item {
  @apply inline-block;
}

.nav-link {
  @apply text-gray-300 hover:text-white-700 px-3 py-2 rounded;
}