html {
  font-size: 14px;
  height: 100%;
  width: 100%;

  @media (min-width: 1600px) {
    font-size: 16px;
  }

  body {
    height: 100%;
    font-family: 'Montserrat', sans-serif;
    background: $color-anti-flash-white;
    div#root {
      // height: 100%;
      // min-height:100%;
      display: flex;
      flex-direction: column;
      height: 100%;
      & > div {
        // height: 100%;

        .page-container {
          display: flex;
          height: 100%;
        }

      }
      .container-block {
        width: 100%;
        padding-right: var(--bs-gutter-x, 0.75rem);
        padding-left: var(--bs-gutter-x, 0.75rem);
        margin-right: auto;
        margin-left: auto;
      }

      .content-body{
        @apply w-full h-full;

        .buildMii-container{
          @apply w-full h-full;
        }

        .discoveryMii-container{
          @apply w-full h-full;
        }
      }
    }
  }
}

@media (min-width: 576px) {
  .container-block {
    max-width: 340px;
  }
}
@media (min-width: 768px) {
  .container-block {
    max-width: 50px;
  }
}
@media (min-width: 992px) {
  .container-block {
    max-width: 760px;
  }
}
@media (min-width: 1200px) {
  .container-block {
    max-width: 1000px;
  }
}
@media (min-width: 1400px) {
  .container-block {
    max-width: 1140px;
  }
}